* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 16px;
    font-family: "roboto", sans-serif;
}

.main {
    display: grid;
    min-height: 100vh;
    grid-template-columns: 2fr 6fr 2fr;
    grid-template-rows: min-content 1fr min-content;
}

@media only screen and (max-width: 900px) {
    .main {
        grid-template-columns: 7fr 3fr;
    }
    .project-card {
        margin-left: .5rem;
    }
}

header {
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 2rem;
    background-color: white;
    border-bottom: 1px solid lightgray;
    box-shadow: 0px 1px 10px 0px gray;
    padding: 1rem clamp(1rem, 15vw, 15rem);
}

.branding {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.logo {
    width: 50px;
    background-color: lightgray;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    padding: .5rem;
    margin-right: 1rem;
}

.logo img {
    width: 100%;
    height: 100%;
}

.title {
    font-size: 2.5rem;
    font-size: clamp(1.5rem, 4vw, 2.5rem);
    color: #cf0404;
    font-weight: bold;
    position: relative;
    top: -1px;
}

@media only screen and (max-width: 650px) {
    .title {
        display: none;
    }
}

nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

nav button {
    border: none;
    background-color: white;
    font-size: 1.5rem;
    color: #cf0404;
    font-weight: bold;
    cursor: pointer;
    padding: 0 1rem;
}

nav button:hover {
    color: gray;
}

footer {
    grid-row: -2 / -1;
    grid-column: 1 / -1;
    display: flex;
    justify-content: center;
    background-color: lightgray;
    padding: 1rem;
    color: #cf0404;
}

.debug {
    border: 1px solid black;
}

.todo-list {
    grid-column: -3 / -2;
    padding-top: 3vw;
}

.project-info, .project-footer {
    background-color: lightgray;
    padding: .5rem 2rem;
    display: flex;
    justify-content: space-evenly;
    font-size: clamp(1rem, 3vw, 1.5rem);
    font-weight: bold;
    border-radius: 20px 20px 0px 0px;
}

.project-info {
    cursor: pointer;
}

.project-info:hover {
    color: #cf0404;
}

.project-footer {
    border-radius: 0px 0px 20px 20px;
}

.project-footer button {
    background-color: inherit;
    border: none;
    font-size: clamp(1rem, 3vw, 1.5rem);
    font-weight: bold;
    cursor: pointer;
}

.project-footer button:hover {
    color: #cf0404;
}

.project-card {
    box-shadow: 0px 0px 4px 0px gray;
    border-radius: 20px;
}

.project-tasks {
    max-height: 450px;
    overflow-x: hidden;
    overflow-y: auto;
}

.task-card {
    display: flex;
    padding: 0rem 0rem 0rem 1rem;
    /* padding: 1rem; */
    justify-content: space-evenly;
    align-items: center;
    font-weight: bold;
    font-size: 1.2rem;
    background-color: white;
    cursor: pointer;
}

.task-title, .project-title {
    max-width: 50%;
    text-wrap: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.task-title {
    width: 35%;
}

.task-duedate {
    /* width: 35%; */
    min-width: 25%;
    /* border: 1px solid black; */
}

.task-card button {
    border: none;
    background-color: inherit;
    font-size: inherit;
    font-weight: inherit;
    cursor: pointer;
    border-radius: 5px;
    font-family: "roboto", sans-serif;
    padding: 1rem 1rem 1rem 0;
}

.task-card button:hover {
    color: #cf0404;
    /* background-color: white; */
}

.not-done::after {
    content: "☐";
    position: relative;
    top: 1px;
    left: 1px;
}

.done::after {
    content: "☑";
    position: relative;
    top: 1px;
    left: 1px;
}

.task-card:hover {
  font-size: 1.5rem;
}

.low-priority {
    background-color: #efff94;
    /* border: 2px solid #60ffff; */
}

.med-priority {
    background-color: #f08358;
}

.high-priority {
    background-color: #db05db;
}

.completed {
    background-color: #55ffa1;
}

.hidden {
    display: none;
}

.popup, .project-popup {
    position: absolute;
    width: fit-content;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 20%;
    background-color: lightgray;
    padding: 1rem 1.5rem;
    border-radius: 20px;
    border: 4px solid #cf0404;
}

.exit-btn-div, .project-popup-exit-div {
    display: flex;
    justify-content: flex-end;
}

.popup label, .project-popup label {
    display: block;
    color: #cf0404;
    font-weight: bold;
    font-size: 1.3rem;
}

.title-date-section {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
}

.popup input, .popup select, .popup textarea, .project-popup input {
    outline: none;
    height: 2.5rem;
    font-size: 1.3rem;
    font-family: "roboto", sans-serif;
    padding: 0 .5rem;
    border: 2px solid #cf0404;
    border-radius: 10px;
    margin-bottom: 1rem;
    background-color: white;
}

.popup textarea {
    outline: none;
    width: 100%;
    height: 9.5ch;
    resize: none;
    padding: .5rem;
}

.del-save-btn-div, .project-btns-div {
    display: flex;
}

.two-btn {
    justify-content: space-between;
}

.one-btn {
    justify-content: flex-end;
}

.popup button, .project-popup button {
    cursor: pointer;
    font-size: 1.3rem;
    border: .2rem solid #cf0404;
    background-color: #cf0404;
    color: white;
    font-weight: bold;
    padding: .4rem;
    border-radius: 10px;
}

.exit-btn-div button, .project-popup-exit-div button {
    padding: .4rem .7rem;
    font-weight: bolder;
}

.popup button:hover {
    background-color: white;
    color: #cf0404;
}

.project-selection {
    margin: 3vw;
}

.project-selection-title {
    margin: .2rem 0;
    font-size: 2rem;
    color: #cf0404;
    font-weight: bold;
}

.project-list {
    max-height: 300px;
    overflow-y: auto;
}

.project-list p {
    font-size: 1.3rem;
    padding: 1rem;
    margin-left: 2px;
    border-left: 3px solid lightgray;
    cursor: pointer;
    max-width: 16ch;
    width: fit-content;
    text-wrap: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.project-list p.project-selected {
    background-color: #ff7373;
    border-left-color: #cf0404;
}

.project-list p:not(.project-selected):hover {
    color: #cf0404;
}

.add-project-btn {
    border: none;
    background-color: white;
    font-size: 1.5rem;
    /* margin-top: .3rem; */
    color: #cf0404;
    font-weight: bold;
    margin-left: .2rem;
    cursor: pointer;
    padding: .5rem 0;
}

.add-project-btn:hover {
    text-decoration: underline;
